.shared-get-started{
    margin-top: 5%;
    padding:7% 15%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-image: url('../../../assets/Home/get-started.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    color:white;
    .block{
        flex: 1;
        margin-right: 20px;
    }
    h1{
        font-size: 35px;
        margin-bottom: 20px;
    }
    p{
        font-size: 18px;
    }
    a{
        font-size: 18px;
        color:#3C72ED;
        background-color: #fff;
        padding:15px 50px;
        border-radius: 28px;
    }
}

@media(max-width:1401px){
    .shared-get-started{
        h1{
            font-size: 30px;
            margin-bottom: 20px;
        }
        p{
            font-size: 15px;
        }
        a{
            font-size: 15px;
            padding:13px 45px;
            border-radius: 25px;
        }
    }
}

@media(max-width:767px){
    .shared-get-started{
        margin-top: 30px;
        padding:35px 7.5%;
        display: block;
        text-align: center;
        .block{
            margin-right: 0px;
        }
        h1{
            font-size: 20px;
            margin-bottom: 15px;
        }
        p{
            font-size: 14px;
            margin-bottom: 30px;
        }
        a{
            font-size: 14px;
            color:#3C72ED;
            padding:13px 30px;
            border-radius: 20px;
        }
    }
}

@-webkit-keyframes hvr-push {
    50% {
      -webkit-transform: scale(0.8);
      transform: scale(0.8);
    }
    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
}
@keyframes hvr-push {
    50% {
      -webkit-transform: scale(0.8);
      transform: scale(0.8);
    }
    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
}

.shared-get-started{
    a{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    }
    a:hover, a:focus, a:active {
        -webkit-animation-name: hvr-push;
        animation-name: hvr-push;
        -webkit-animation-duration: 0.3s;
        animation-duration: 0.3s;
        -webkit-animation-timing-function: linear;
        animation-timing-function: linear;
        -webkit-animation-iteration-count: 1;
        animation-iteration-count: 1;
    }
}  