.shared-banner{
    padding:12.5% 10%;
    color:#fff;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    .inner-banner{
        width: 40%;
    }
    h5{
        font-size: 18px;
        padding:8px 25px;
        border-radius: 20px;
        background:linear-gradient(rgba(255,255,255,0.25), rgba(255,255,255,0.25));
        width: fit-content;
    }
    h1{
        font-size: 45px;
        margin:20px 0;
    }
    p{
        font-size: 18px;
        margin-bottom: 30px;
    }
}

@media(max-width:1401px){
    .shared-banner{
        padding:12.5% 10%;
        .inner-banner{
            width: 40%;
        }
        h5{
            font-size: 15px;
            padding:8px 20px;
            border-radius: 20px;
        }
        h1{
            font-size: 35px;
            margin:20px 0;
        }
        p{
            font-size: 15px;
            margin-bottom: 30px;
        }
    }
}

@media(max-width:767px){
    .shared-banner{
        padding:130px 7.5% 100px;
        background-attachment: unset;
        .inner-banner{
            width: 100%;
            text-align: center;
        }
        h5,a{
            margin:0 auto;
        }
        h1{
            font-size: 25px;
            margin:15px 0;
        }
        p{
            margin-bottom: 20px;
        }
    }
}

