.plans-banner{
    // clip-path: polygon(30% 0%, 70% 0%, 100% 0, 100% 85%, 70% 100%, 30% 100%, 0 85%, 0 0);
    padding:10% 10% 8%;
    text-align: center;
    width: 110%;
    margin-left: -5%;
    border-bottom-left-radius: 50% 35%;
    border-bottom-right-radius: 50% 35%;

    .inner-banner{
        width: 100%;
    }
    h5,a{
        margin:0 auto;
    }
    p{
        width: 40%;
        margin:0 auto 30px;
    }
    .card{
        margin-top: 35px;
        font-size: 17px;
    }
}

@media(max-width:1401px){
    .plans-banner{
        border-bottom-left-radius: 50% 25%;
        border-bottom-right-radius: 50% 25%;
        padding:10% 10% 6%;
        p{
            width: 45%;
            margin:0 auto 30px;
        }
        .card{
            margin-top: 30px;
            font-size: 14px;
        }
    }
}

@media(max-width:767px){
    .plans-banner{
        border-bottom-left-radius: 50% 15%;
        border-bottom-right-radius: 50% 15%;
        padding:125px 12.5% 75px;
        p{
            width: 100%;
            margin:0 auto 25px;
        }
    }
}