.families-page .features .row3{
    margin:7.5% 0;
    padding:0 7.5%;
    h1{
        font-size: 35px;
        margin-bottom: 45px;
    }
    .inner-row{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .items{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 57.5%;
    }
    .img{
        width: 35%;
        content: url('../../../../assets/Families/img4.png');
        border-radius: 10px;
        border:1px solid #eee;
    }
    .item3{
        padding:55px 35px;
        border:1px solid #f5f5f5;
        box-shadow: rgba(0, 0, 0, 0.095) 2px 2px 3px;
        width: 45%;
        border-radius: 10px;
        margin:20px 0;
        svg{
            color:#427cff;
            font-size:45px;
        }
        h6{
            font-size: 18px;
            margin:17.5px 0;
        }
        p{
            font-size: 17px;
        }
        &:hover{
            background: linear-gradient(#427CFF,#213E80);
            p,h6, svg{
                color:#fff;
            }
        }
    }
}

@media(max-width:1401px){
    .families-page .features .row3{
        h1{
            font-size: 27.5px;
            margin-bottom: 25px;
        }
        .items{
            width: 57.5%;
        }
        .img{
            width: 35%;
        }
        .item3{
            padding:35px 25px;
            box-shadow: rgba(0, 0, 0, 0.05) 1.5px 1.5px 2px;
            width: 47.5%;
            margin:15px 0;
            svg{
                font-size:40px;
            }
            h6{
                font-size: 15px;
                margin:15px 0;
            }
            p{
                font-size: 15px;
            }
        }
    }
}


@media(max-width:991px){
    .families-page .features .row3{
        h1{
            font-size: 20px;
            text-align: center;
            margin-bottom: 5px;
        }
        .inner-row{
            flex-wrap: wrap;
        }
        .items{
            width: 100%;
            margin:15px auto;
        }
        .img{
            width: 400px;
            max-width: 100%;
            margin:15px auto;
        }
    }
}
@media(max-width:991px){
    .families-page .features .row3{
        .item3{
            padding:25px;
            width: 100%;
            margin:10px 0;
        }
    }
}



