.shared-values{
    margin:5% 0;
    padding:0 10%;
    .p{
        font-size: 18px;
        color:#555;
        width:40%;
        text-align: center;
        margin:0 auto 45px;
    }
    .row1{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        padding-bottom: 30px;
        border-bottom: 1px solid #ccc;
    }
    .card{
        text-align: center;
        width:28%;
        margin:0 2.5%;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: scale(1.045);
        }
    }
    h5{
        color:#427cff;
        font-size: 50px;
        margin-bottom: 10px;
        span{
            margin-left: 10px;
        }
        svg{
            margin-bottom: -7px;
            font-size: 40px;
        }
    }
    .card p{
        font-size: 20px;
    }
}

@media(max-width:1401px){
    .shared-values{
        .p{
            font-size: 15px;
            width:45%;
            margin:0 auto 40px;
        }
        .row1{
            padding-bottom: 25px;
        }
        h5{
            font-size: 37.5px;           
            svg{
                margin-bottom: -5px;
                font-size: 30px;
            }
        }
        .card p{
            font-size: 16px;
        }
    }
}

@media(max-width:767px){
    .shared-values .card{
        width: 45%;
        margin:10px 2.5%;
        h5{
            font-size: 30px;
            svg{
                font-size: 25px;
            }
        }
    }
    .shared-values{ 
        margin:50px 0;
        padding:0 7.5%;
        .p{
            width: 100%;
            margin-bottom: 20px;
        }
    }
}


@media(max-width:420px){
    .shared-values .card{
        width: 100%;
        h5{
            margin-bottom: 5px;
        }
    }
}