.shared-hexagon-item{
    display: flex;
    align-items: center;
    margin:35px 0;
    .icon{
         clip-path: polygon(22% 0, 78% 0, 100% 50%, 78% 100%, 22% 100%, 0% 50%);
         width:90px;
         height: 80px;
         font-size: 40px;
         margin-right: 20px;
         background-color: #e6edff;
         display: flex;
         align-items: center;
         justify-content: center;
         color:#427cff;
    }
    .info{
        flex: 1;
        h6{
            font-size: 19px;
            margin-bottom: 7.5px;
        }
        p{
            font-size: 18px;
        }
    }
    &:hover{
        .icon{
            background-color: #427cff;
            color:#fff;
        }
    }
}

@media(max-width:1401px){
    .shared-hexagon-item{
        margin:30px 0;
        .icon{
             clip-path: polygon(22% 0, 78% 0, 100% 50%, 78% 100%, 22% 100%, 0% 50%);
             width:75px;
             height: 65px;
             font-size: 35px;
             margin-right: 15px;
        }
        .info{
            h6{
                font-size: 16px;
                margin-bottom: 7.5px;
            }
            p{
                font-size: 15px;
            }
        }
    }
}

@media(max-width:1401px){
    .shared-hexagon-item{
        margin:25px 0;
        .icon{
             clip-path: polygon(22% 0, 78% 0, 100% 50%, 78% 100%, 22% 100%, 0% 50%);
             width:70px;
             height: 60px;
             font-size: 30px;
             margin-right: 12.5px;
        }
        .info{
            h6{
                font-size: 15px;
                margin-bottom: 5px;
            }
            p{
                font-size: 15px;
            }
        }
    }
}