.families-page{
    overflow: hidden;
}

.families-page .share-password .row1{
    margin:5% 0;
    padding: 0 10% 0 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    .info{
        width: 35%;
    }
    .images{
        width: 50%;
        position: relative;
    }
    .top-img{
        content: url('../../../../assets/Families/img1.png');
        width: 115%;
        margin-left: -15%;
        position: relative;
        z-index: 2;
    }
    .bg-img{
        content: url('../../../../assets/Families/logo.png');
        position: absolute;
        width: 40%;
        right:0;
        bottom:0;
    }
    h1{
        font-size: 35px;
        margin-bottom: 20px;
    }
    p{
        font-size: 20px;
    }
}

@media(max-width:1401px){
    .families-page .share-password .row1{
        margin:5% 0 7.5%;
        .info{
            width: 37.5%;
        }
        h1{
            font-size: 27.5px;
            margin-bottom: 15px;
        }
        p{
            font-size: 16px;
            line-height: 1.5;
        }
    }
}

@media(max-width:767px){
    .families-page .share-password .row1{
        margin:30px 0;
        padding:0 7.5%;
        flex-direction: column-reverse;
        .info, .images{
            width: 100%;
            margin:20px 0;
        }
        .top-img{
            width: 100%;
            margin:0;
        }
        h1{
            font-size: 20px;
        }
    }
}

//================================================
.families-page .share-password .row2{
    margin:7.5% 0;
    padding:0 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    .info2{
        width: 41%;
    }
    .images{
        width: 41%;
        position: relative;
    }
    .bg-img{
        content: url('../../../../assets/Families/bg1.png');
        width: 100%;
    }
    .top-img{
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        width: 82.5%;
        border-radius: 50%;
        transition: 0.3s ease-in-out;
        &:hover{
            border-radius: 10%;
        }
    }
    .top-img1{
        content: url('../../../../assets/Families/img2.png');
    }
    .top-img2{
        content: url('../../../../assets/Families/img3.png');
    }
    h1{
        font-size: 35px;
        margin-bottom: 10px;
    }
    .link{
        margin-top: 45px;
    }
}

@media(max-width:1401px){
    .families-page .share-password .row2{
        .info2{
            width: 45%;
        }
        h1{
            font-size: 27.5px;
        }
        .link{
            margin-top: 35px;
        }
    }
}

@media(max-width:991px){
    .families-page .share-password .row2{
        padding:0 7.5%;
        margin:10% 0;
        &:last-child{
            flex-direction: column-reverse;
        }
        .info2{
            width: 47.5%;
        }
        .images{
            width: 45%;
        }
    }
}

@media(max-width:750px){
    .families-page .share-password .row2{
        padding:0 7.5%;
        margin:30px 0;
        .images{
            width:350px;
            max-width: 100%;
            margin: 20px auto;
        }
        .info2{
            width:100%;
            margin: 20px 0;
        }
        h1{
            font-size: 22.5px;
        }
        .link{
            margin-top: 20px;
        }
    }
}