.shared-users-comment{
    margin:5% 0;
    padding:0 7.5%;
    position: relative;
    .rec-arrow,
    .rec-arrow-left,
    .rec-arrow-right{
        display: none;
    }
    .quotes{
        color:#cddcff;
        font-size: 65px;
        text-align: center;
        position: relative;
        z-index: 2;
    }
    h1{
        font-size: 35px;
        text-align: center;
        margin:15px 0 75px;
        position: relative;
        z-index: 2;
    }
    .slider-bg{
        content: url('../../../assets/Home/slider-bg.png');
        position: absolute;
        width: 27%;
        bottom:50px;
        left:0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
    }
    .slider-item{
        z-index: 2;
        overflow: hidden;
        background:#fff;
        width: 85%;
        border:1px solid #eee;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 9px -4px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
        border-radius: 10px;
        margin:10px auto 30px;
        position: relative;
        padding:30px;
    }
    .profile{
        width: 80px;
    }
    .line{
        width: 80px;
        height: 2.5px;
        background-color: #427CFF;
        margin-top: 15px;
    }
    p{
        font-size: 18px;
        margin:15px 0 60px;
    }
    .name{
        display: flex;
        align-items: center;
        position: absolute;
        bottom:25px;
    }
    h6{
        font-size: 19px;
        color: #427CFF;
        margin-right: 5px;
    }
    small{
        font-size: 17px;
        color:#427CFF;
        margin-bottom: -1px;
    }
}

@media(max-width:1401px){
    .shared-users-comment{
        .quotes{
            font-size: 45px;
        }
        h1{
            font-size: 30px;
            margin:5px 0 50px;
        }
        .slider-bg{
            width: 32%;
            bottom:50px;
        }
        .slider-item{
            width: 90%;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 5px -3px, rgba(0, 0, 0, 0.05) 0px 3px 5px -2px;
            margin:10px auto 20px;
            padding:22px;
        }
        .profile{
            width: 60px;
        }
        .line{
            width: 60px;
        }
        p{
            font-size: 15px;
            margin:15px 0 50px;
        }
        .name{
            bottom:22px;
        }
        h6{
            font-size: 16px;
        }
        small{
            font-size: 14px;
        }
    }
}

@media(max-width:767px){
    .shared-users-comment{
        margin:45px 0;
        padding:0 5%;
        .quotes{
            font-size: 40px;
        }
        h1{
            font-size: 20px;
            margin:5px 0 15px;
        }
        .slider-bg{
            width: 65%;
            bottom:40px;
        }
        .slider-item{
            width: 95%;
            box-shadow: rgba(0, 0, 0, 0.1) 1.95px 1.95px 1px;
            margin:10px auto 15px;
            padding:20px;
        }
        .profile{
            width: 55px;
        }
        .line{
            width: 55px;
            height: 2px;
            margin-top: 15px;
        }
        p{
            font-size: 14px;
            margin:15px 0 45px;
        }
        .name{
            bottom:17.5px;
        }
        h6{
            font-size: 15px;
            margin-right: 5px;
        }
        small{
            font-size: 13px;
            margin-bottom: -1px;
        }
    }    
}

.shared-users-comment{
    .slider-item{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        
        transition: 0.35s ease-in-out;
        &:hover{
            transform: translateY(-10px);
        }
    }
    .slider-item:before{
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0px;
        background: #427CFF;
        height: 5px;
        -webkit-transition-property: left, right;
        transition-property: left, right;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
        border-radius: 0 0 15px 15px;
    }
    .slider-item:hover:before{
        left: 0;
        right: 0;
    }
}